* {
    margin: 0;
    padding: 0
}

:focus {
    outline: none
}

body {
    scroll-behavior: smooth;
    background-color: gray
}

img {
    width: 100%;
    height: auto;
    line-height: 0;
    display: block
}

a {
    text-decoration-line: none
}

ul {
    list-style-type: none
}